*{box-sizing:border-box}body{margin:0;padding:0;font-family:'Rubik', 'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#000}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html{font-size:16px}@media (max-width: 900px){html{font-size:12px}}@media (max-width: 400px){html{font-size:10px}}

body{height:100vh}.absolute-center,.game{position:absolute;top:45%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.game .title{position:absolute;top:calc(100% + 150px);width:100%;text-align:center;color:#d3d3d3;font-family:"Bangers",cursive;font-size:4rem}.game .winner{position:absolute;top:calc(100%);width:100%;text-align:center;color:#d3d3d3;font-family:"Bangers",cursive;font-size:2rem}.game .reset-btn{position:absolute;top:calc(100% - 15px);background:none;padding:0.7rem;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);border:1px solid #d3d3d3;color:#d3d3d3;font-family:"Bangers",cursive;font-size:2rem;cursor:pointer}.game .reset-btn:hover{background:linear-gradient(45deg, #f0c 20%, #9D00FF)}

body{height:100vh}.absolute-center{position:absolute;top:45%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.grid{display:grid;grid-gap:0px;width:40vw;grid-template-columns:repeat(3, 1fr)}@media (max-width: 900px){.grid{width:50vw}}

.tile{height:calc(40vw / 3);background-color:transparent;color:#d3d3d3;border:none;border-right:10px solid #d3d3d3;border-bottom:10px solid #d3d3d3}.tile:nth-child(3n){border-right:none}.tile:nth-last-child(-n+3){border-bottom:none}@media (max-width: 900px){.tile{height:calc(50vw / 3)}}

.X{width:20%;height:75%;background-color:#f0c;-webkit-transform:translateX(-50%) rotate(45deg);transform:translateX(-50%) rotate(45deg);position:relative;left:50%}.X::after{content:'';position:absolute;left:0;top:0;box-sizing:border-box;-webkit-transform:rotate(-90deg);transform:rotate(-90deg);background-color:#f0c;width:100%;height:100%}

.O{width:75%;height:75%;border-radius:50%;border:40px solid #9D00FF;position:relative;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}@media (max-width: 900px){.O{border-width:6px}}

